<template>
	<li>
		<label>
			<!-- 要確認done狀態，要抓到todo.id -->
			<input type="checkbox" :checked="todo.done" @change="handleCheck(todo.id)"/>
			<!--todo.done 默認勾著  -->
			<!-- 如下代碼也能實現功能，但是不太推薦，因為有點違反原則，因為修改了props -->
			<!-- <input type="checkbox" v-model="todo.done"/> -->

			<!-- 這裡的v-show 為了編輯功能 -->
			<span v-show="!todo.isEdit">{{todo.title}}</span>

			<!-- 這裡的v-show=todo.isEdit 為真時，出現的編輯框 -->
			<input type="text" v-show="todo.isEdit" :value="todo.title" 
				@blur="handleBlur(todo,$event)" @keydown.enter="handleBlur(todo,$event)" ref="inputTitle">

		</label>
		<button class="btn btn-danger" @click="handleDelete(todo.id)">刪除</button>
		<button v-show="!todo.isEdit" class="btn btn-edit" @click="handleEdit(todo)">編輯</button>
	</li>
</template>

<script>
	import pubsub from 'pubsub-js'
	export default {
		name:'MyItem',
		//聲明接收todo、checkTodo、deleteTodo
		props:['todo','checkTodo','deleteTodo'],
		methods: {
			//勾選or取消勾選
			handleCheck(id){
				//通知App組件將對應的todo對象的done值取反，子[MyItem]組件數據給父[App]組件
				// this.checkTodo(id)

				//全局事件組件寫法，提供數據
				this.$bus.$emit('checkTodo',id)
			},
			//刪除
			handleDelete(id){
				if(confirm('確定刪除嗎？')){

					//通知App組件將對應的todo對象刪除[deleteTodo =>函數名]
					// this.deleteTodo(id)

					//全局事件組件寫法，提供數據 [deleteTodo =>事件名]
					// this.$bus.$emit('deleteTodo',id)

					//pubsub組件寫法，提供數據 [deleteTodo =>消息名]，第三方庫vue不支持，在vue devtools 看不出來
					pubsub.publish('deleteTodo',id)
				}
			},
			//編輯，傳todo，因為要修改todo中，title的數據
			handleEdit(todo){
				// hasOwnProperty() 回傳物件是否有該屬性的布林值。
				if(todo.hasOwnProperty('isEdit')){
					todo.isEdit = true
				}else{
					// console.log('@')
					//給todo 身上加一個isEdit參數，這樣設置的參數才會有getter & setter
					this.$set(todo,'isEdit',true)
				}

				//先去重新解析完模板後，再去調用nextTick中的函數
				// nextTick() 可以在狀態改變後立即使用，以等待 DOM 更新完成。
				// 你可以傳遞一個回調函數作為參數，或者 await 返回的 Promise
				this.$nextTick(function(){
					this.$refs.inputTitle.focus()
				})
			},
			//失去焦點的回調（真正執行修改的邏輯）
			handleBlur(todo,e){
				todo.isEdit = false

				// trim() 方法會移除字串起始及結尾處的空白字元。
				if(!e.target.value.trim()) return alert('輸入不能為空！')
				// e.target.value最新的值
				this.$bus.$emit('updateTodo',todo.id,e.target.value)
			}
		},
	}
</script>

<style scoped>
	/*item*/
	li {
		list-style: none;
		height: 36px;
		line-height: 36px;
		padding: 0 5px;
		border-bottom: 1px solid #ddd;
	}

	li label {
		float: left;
		cursor: pointer;
	}

	li label li input {
		vertical-align: middle;
		margin-right: 6px;
		position: relative;
		top: -1px;
	}

	li button {
		float: right;
		display: none;
		margin-top: 3px;
	}

	li:before {
		content: initial;
	}

	li:last-child {
		border-bottom: none;
	}

	li:hover{
		background-color: #ddd;
	}
	
	li:hover button{
		display: block;
	}
</style>