<template>
	<div class="todo-header">
		<input type="text" placeholder="請輸入你的任務名稱，按回車鍵確認" 
		v-model="title" @keyup.enter="add"/>
	</div>
</template>

<script>
	import {nanoid} from 'nanoid'//生成唯一的字符串，用來模擬用戶id
	export default {
		name:'MyHeader',
		//接收從App[父]傳遞過來的addTodo函數
		// props:['addTodo'],
		data() {
			return {
				//收集用戶輸入的title，抓v-model的那邊的數據
				title:''
			}
		},
		methods: {
			add(){//用戶輸入包裝成一個todo object

				//校驗數據
				// trim() 方法會移除字串起始及結尾處的空白字元。 
				// 本文中的空白字元指所有空格字元（如：空格、欄標、無間斷空格等等）及換行字元（如：換行、回車等等）。
				if(!this.title.trim()) return alert('輸入不能為空')

				//將用戶的輸入包裝成一個todo對象
				const todoObj = {id:nanoid(),title:this.title,done:false}

				//通知App組件去添加一個todo對象
				// this.addTodo(todoObj) //props 子傳父
				this.$emit('addTodo',todoObj,1,2,3) //自定義事件 子傳父

				//清空輸入
				this.title = ''
			}
		},
	}
</script>

<style scoped>
	/*header*/
	.todo-header input {
		width: 560px;
		height: 28px;
		font-size: 14px;
		border: 1px solid #ccc;
		border-radius: 4px;
		padding: 4px 7px;
	}

	.todo-header input:focus {
		outline: none;
		border-color: rgba(82, 168, 236, 0.8);
		box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
	}
</style>