<template>
	<div id="root">
		<div class="todo-container">
			<div class="todo-wrap">
				<!-- 引入該封裝的組件的文件叫做父組件，被引入的組件叫做子組件 -->
				<!-- 新增todolist項目，props：子組件 ==> 父組件 通信（要求父[app]先給子[myheader]一個函數） -->
				<!-- @addTodo自定義事件，子組件 ==> 父組件 通信 -->
				<!-- <MyHeader :addTodo="addTodo"/> -->
				<MyHeader @addTodo="addTodo"/>
				<!-- check、刪 -->
				<MyList :todos="todos" :checkTodo="checkTodo" :deleteTodo="deleteTodo"/>
				<!-- checkall clearall -->
				<!-- <MyFooter :todos="todos" :checkAllTodo="checkAllTodo" :clearAllTodo="clearAllTodo"/> -->
				<MyFooter :todos="todos" @checkAllTodo="checkAllTodo" @clearAllTodo="clearAllTodo"/>
			</div>
		</div>
	</div>
</template>

<script>
	import pubsub from 'pubsub-js'
	//header盡量這種關鍵字不要用
	import MyHeader from './components/MyHeader'
	import MyList from './components/MyList'
	import MyFooter from './components/MyFooter.vue'

	export default {
		name:'App',
		components:{MyHeader,MyList,MyFooter},
		data() {
			return {
				//由於todos是MyHeader組件和MyFooter組件都在使用，所以放在App中（狀態提升）
				// todos:[
				// 	{id:'001',title:'西西',done:true},
				// 	{id:'002',title:'咚咚',done:false},
				// 	{id:'003',title:'拉拉',done:true}
				// ]
				//getItem storage中的API
				todos:JSON.parse(localStorage.getItem('todos')) || []
			}
		},
		methods: {
			//添加一個todo
			addTodo(todoObj){
				// unshift() 方法會添加一個或多個元素至陣列的開頭，並且回傳陣列的新長度。
				this.todos.unshift(todoObj)
			},
			//勾選or取消勾選一個todo
			checkTodo(id){
				this.todos.forEach((todo)=>{
					if(todo.id === id) todo.done = !todo.done
				})
			},
			//更新一个todo
			updateTodo(id,title){
				this.todos.forEach((todo)=>{
					if(todo.id === id) todo.title = title
				})
			},
			//刪除一個todo
			deleteTodo(_,id){ //用下划線於，有參數，但不用的場合，用佔位保證收到的參數正確
				this.todos = this.todos.filter( todo => todo.id !== id )
			},
			//全選or取消全選
			checkAllTodo(done){
				this.todos.forEach((todo)=>{
					todo.done = done
				})
			},
			//清除所有已經完成的todo
			clearAllTodo(){
				this.todos = this.todos.filter((todo)=>{
					return !todo.done
				})
			}
		},
		watch:{
			todos:{
				deep:true,
				handler(value){//數值發生改變就會觸發
					//setItem storage中的API
					localStorage.setItem('todos',JSON.stringify(value))
				}
			}
		},
		mounted(){
			//全局事件總線，接收數據
			this.$bus.$on('checkTodo',this.checkTodo)
			// this.$bus.$on('deleteTodo',this.deleteTodo)

			//用刪除模擬pubsub
			this.pubId=pubsub.subscribe('deleteTodo',this.deleteTodo)

			this.$bus.$on('updateTodo',this.updateTodo)
		},
		beforeDestroy() {
			//全局事件總線，解綁
			this.$bus.$off('checkTodo')
			// this.$bus.$off('deleteTodo')

			//用刪除模擬pubsub
			pubsub.unsubscribe(this.pubId)

			this.$bus.$off('updateTodo')
		},
	}
</script>

