<template>
	<ul class="todo-main">
		<!-- <MyItem 
			v-for="todoObj in todos" :key="todoObj.id"  
			:todo="todoObj" 
			:checkTodo="checkTodo" :deleteTodo="deleteTodo"
		/> -->
		<transition-group name="todo" appear>
			<MyItem v-for="todoObj in todos" :key="todoObj.id" 
			:todo="todoObj" :checkTodo="checkTodo" :deleteTodo="deleteTodo"></MyItem>
		</transition-group>
	</ul>
</template>

<script>
	import MyItem from './MyItem'

	export default {
		name:'MyList',
		components:{MyItem},
		//聲明接收App傳遞過來的數據，其中todos是自己用的，checkTodo和deleteTodo是給子組件MyItem用的
		props:['todos','checkTodo','deleteTodo']
	}
</script>

<style scoped>
	/*main*/
	.todo-main {
		margin-left: 0px;
		border: 1px solid #ddd;
		border-radius: 2px;
		padding: 0px;
	}

	.todo-empty {
		height: 40px;
		line-height: 40px;
		border: 1px solid #ddd;
		border-radius: 2px;
		padding-left: 5px;
		margin-top: 10px;
	}

	.todo-enter-active{
		animation: atguigu 0.5s linear;
	}

	.todo-leave-active{
		animation: atguigu 0.5s linear reverse;
	}

	@keyframes atguigu {
		from{
			transform: translateX(100%);
		}
		to{
			transform: translateX(0px);
		}
	}
</style>